import { writable } from 'svelte/store';
// Overall app init
export const INIT_FAILED = writable(false);
// Market
export const MARKET = writable({
    /** True when all market and reserve account subscriptions have returned data at least once. */
    marketInit: false,
    // Accounts
    accountPubkey: {},
    authorityPubkey: {},
    // Hardcode minimum c-ratio to 130% for now
    minColRatio: 1.3,
    // Hardcode minimum c-ratio to 130% for now
    programMinColRatio: 1.25,
    // Total value of all reserves
    totalValueLocked: 0,
    // Reserves
    reserves: {},
    reservesArray: [],
    currentReserve: {},
    /** Native vs USD UI values */
    nativeValues: true,
});
// User
let user;
export const USER = writable({
    // Locale
    locale: null,
    geobanned: false,
    // Wallet
    connectingWallet: true,
    wallet: null,
    walletInit: false,
    tradeAction: 'deposit',
    // Assets and position
    assets: null,
    walletBalances: {},
    collateralBalances: {},
    loanBalances: {},
    position: {
        depositedValue: 0,
        borrowedValue: 0,
        colRatio: 0,
        utilizationRate: 0
    },
    // Transaction Logs
    transactionLogs: [],
    transactionLogsInit: true,
    // Notifications
    notifications: [],
    // Add notification
    addNotification: (n) => {
        const notifs = user.notifications ?? [];
        notifs.push(n);
        const index = notifs.indexOf(n);
        USER.update(user => {
            user.notifications = notifs;
            return user;
        });
        setTimeout(() => {
            if (user.notifications[index] && user.notifications[index].text === n.text) {
                user.clearNotification(index);
            }
        }, 5000);
    },
    // Clear notification
    clearNotification: (i) => {
        const notifs = user.notifications;
        notifs.splice(i, 1);
        USER.update(user => {
            user.notifications = notifs;
            return user;
        });
    },
    // Settings
    darkTheme: localStorage.getItem('jetDark') === 'true',
    navExpanded: localStorage.getItem('jetNavExpanded') === 'true',
    language: localStorage.getItem('jetPreferredLanguage') ?? 'en',
    rpcNode: localStorage.getItem('jetPreferredNode') ?? '',
    rpcPing: 0,
});
USER.subscribe(data => user = data);
// Copilot
export const COPILOT = writable(null);
// Program
export const PROGRAM = writable(null);
export const CUSTOM_PROGRAM_ERRORS = writable([]);
export const CONNECTION = writable(undefined);
export const ANCHOR_CODER = writable(undefined);
export const IDL_METADATA = writable(undefined);
